export default {
	name:'group',
	data() {
		return {
			grouplist:[]
		}
	},
	mounted(){
		var that=this
		uni.request({
			url: getApp().globalData.APIurl+'/api/wxapp/member/groupList',
			data: {
			},
			header: {
			    'Token': that.token ,
				'Device-Type':'wxapp'
			},
			success: (res) => {
				if(res.data.code){
					that.grouplist=res.data.data.list
				}
			}
		})
	},
}
